$text_color: blue;
@mixin boxx {
    display: inline-block;
    width: 600px;
    height: 300px;
    background: blue; }
@mixin box {
    display: block;
    width: 250px;
    height: 200px;
    background: green; }




h1 {
    color: $text_color;
    font-style: 50px;
    @include box; }
h2 {
    color: blue; }
.blok {
    background-color: black;
    h3 {
        color: $text_color; }
    &_color {
        background: $text_color;
        .btn {
            @include box; } }
    .blok_color {
        @include boxx;
        color: $text_color; } }
